<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      buyerId: null,
      itemDialog: false,
      likeDialog: false,
      likedLists: [],
      likedList: null,
      itemId: null,
      itemType: '',
      imageURL: '',
      name: '',
      description: '',
      priceSGD: null,
      itemDiscount: null,
      deliveryTimeDays: null,
      deliveryTimeHrs: null,
      listPeriod: null,
      qty: 1,
      rules: {
        qty: [val => val > 0 || 'Enter 1 or more in quantity please.']
      }
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      items: 'getUpdatedItems',
      likedItems: 'getLikedItems',
      cartItems: 'getCartItems'
    }),
    itemsLoaded () {
      return this.items != null || undefined
    },
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    discount () {
      return this.itemDiscount > 0
    },
    discountedPrice () {
      return (this.priceSGD * (1 - this.itemDiscount / 100)).toFixed(2)
    },
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    },
    likedListLoaded () {
      if (this.likedLists === null || undefined) {
        return false
      } else if (this.likedLists.length === 0) {
        return false
      } else if (this.likedLists[0] === null) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    // initialization functions to be called on mounted lifecycle hook
    init () {
      // load all available items
      this.$store.dispatch('itemsApi')
      if (this.user) {
        this.buyerId = this.user.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      } else if (this.vendor) {
        this.buyerId = this.vendor.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    // dismiss alerts
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    },
    // item dialog methods
    openDialog (item) {
      // open itemDialog
      this.itemDialog = true
      // set dialog params
      this.itemId = item.itemId
      this.imageURL = item.imageURL
      this.name = item.itemName
      this.description = item.description
      this.itemType = item.itemType
      this.priceSGD = item.priceSGD
      this.itemDiscount = item.discount
      this.listPeriod = item.listPeriod
      this.deliveryTimeDays = item.deliveryTimeDays
      this.deliveryTimeHrs = item.deliveryTimeHrs
      this.qty = item.qty
      this.$store.dispatch('setError', {message: 'Only consumers 18 years old and above may purchase and consume alcohol! Please enjoy responsibly.'})
    },
    closeDialog () {
      // close itemDialog
      this.itemDialog = false
      this.onDismissed()
      // reset dialog params
      this.itemId = ''
      this.imageURL = ''
      this.name = ''
      this.description = ''
      this.priceSGD = null
      this.itemDiscount = null
      this.listPeriod = null
      this.deliveryTimeDays = null
      this.deliveryTimeHrs = null
      this.qty = 1
    },
    seeLikedListsFromShop (item) {
      // set params to capture all wishlists, and count them by freq
      let likedListsArr = []
      // reset likedLists array
      this.likedLists = []
      // set unique likedlist name and count arrays
      let listNames = []
      let listCount = []
      let prev = ''

      // set get likedList function
      function getLikedLists (likedItem) {
        var likedList = likedItem.likedList
        return likedList
      }
      if (this.buyerId) {
        // set like dialog params
        this.itemId = item.itemId
        this.name = item.itemName
        this.description = item.description
        this.imageURL = item.imageURL
        this.priceSGD = item.priceSGD
        this.itemDiscount = item.discount
        // get all likedItem lists from likedItems array
        likedListsArr = this.likedItems.map(getLikedLists)
        // sort likedLists in ascending alpha order
        likedListsArr.sort()
        // loop over sorted likedListsArr to push unique list names into listNames array and their frequencies into count array
        for (var i = 0; i < likedListsArr.length; i++) {
          if (likedListsArr[i] !== prev) {
            listNames.push(likedListsArr[i])
            listCount.push(1)
          } else {
            listCount[listCount.length - 1]++
          }
          prev = likedListsArr[i]
        }
        // merge each corresponding elements in arrays listNames and listCount into an object and set it to likedLists array
        const resultArray = listNames.map((value, index) => ({
          name: value,
          count: listCount[index]
        }))
        this.likedLists = resultArray
        // open like item dialog
        this.likeDialog = true
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your wishlist!'})
      }
    },
    seeLikedListsFromDialog () {
      // set params to capture all wishlists, and count them by freq
      let likedListsArr = []
      // set unique likedlist name and count arrays
      let listNames = []
      let listCount = []
      let prev = ''

      // reset likedLists array
      this.likedLists = []
      // set get likedList function
      function getLikedLists (likedItem) {
        var likedList = likedItem.likedList
        return likedList
      }
      if (this.buyerId) {
        // get all likedItem lists from likedItems array
        likedListsArr = this.likedItems.map(getLikedLists)
        // sort likedLists in ascending alpha order
        likedListsArr.sort()
        // loop over sorted likedListsArr to push unique list names into listNames array and their frequencies into count array
        for (var i = 0; i < likedListsArr.length; i++) {
          if (likedListsArr[i] !== prev) {
            listNames.push(likedListsArr[i])
            listCount.push(1)
          } else {
            listCount[listCount.length - 1]++
          }
          prev = likedListsArr[i]
        }
        // merge each corresponding elements in arrays listNames and count into an object and set it to likedLists array
        const resultArray = listNames.map((value, index) => ({ name: value, count: listCount[index] }))
        this.likedLists = resultArray
        // open like item dialog
        this.likeDialog = true
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your wishlist!'})
      }
    },
    openNewListForm () {
      this.likedList = 'Name your list'
    },
    createList () {
      this.like(this.likedList)
      this.likeDialog = false
      this.likedList = null
    },
    deleteList () {
      this.likedList = null
    },
    like (likedListName) {
      let payload = {
        userId: this.buyerId,
        itemId: this.itemId,
        likedList: likedListName
      }
      // console.log(payload)
      this.$store.dispatch('likeItem', payload)
      this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
      this.likeDialog = false
    },
    unlike (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('unlikeItem', payload)
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
      }
    },
    likedItem (itemId) {
      if (this.buyerId) {
        if (this.likedItems === null || undefined) {
          return false
        } else {
          let liked = false
          for (let i = 0; i < this.likedItems.length; i++) {
            if (this.likedItems[i].itemId === itemId) {
              liked = true
              break
            }
          }
          return liked
        }
      } else {
        return false
      }
    },
    likedItemInList (likedList) {
      if (this.likedItems === null || undefined) {
        return false
      } else {
        let liked = false
        for (let i = 0; i < this.likedItems.length; i++) {
          if (this.likedItems[i].likedList === likedList) {
            liked = true
            break
          }
        }
        return liked
      }
    },
    addToCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId,
          qty: this.qty
        }
        this.$store.dispatch('addItemToCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
        this.getItemQty(payload)
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your shopping cart!'})
      }
    },
    removeFromCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('removeItemFromCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    addedToCart (itemId) {
      if (this.buyerId) {
        if (this.cartItems === null || undefined) {
          return false
        } else {
          let added = false
          for (let i = 0; i < this.cartItems.length; i++) {
            this.getItemQty(this.cartItems[i])
            if (this.cartItems[i].itemId === itemId) {
              added = true
              break
            }
          }
          return added
        }
      } else {
        return false
      }
    },
    qtyValid () {
      if (this.qty > 0) {
        return true
      } else {
        return false
      }
    },
    getItemQty (payload) {
      for (let i = 0; i < this.items.length; i++) {
        if (this.items[i].itemId === payload.itemId) {
          this.items[i].qty = payload.qty
          break
        }
      }
    },
    setItemQty (itemQty) {
      this.qty = itemQty
    }
  }
}
</script>




